import React, { useState } from 'react';

import IconButton from '#components/IconButton';

import EyeHiddenSVG from '#assets/icons/eye-hidden-solid-24.svg';
import EyeVisibleSVG from '#assets/icons/eye-visible-solid-24.svg';

import TextInput from '../TextInput/TextInput';

import { PasswordInputProps } from './PasswordInput.types';

const PasswordInput: React.FC<PasswordInputProps> = (inputProps) => {
  const [showPassword, setShowPassword] = useState(false);

  const toggleShowPassword = () => {
    setShowPassword((prev) => !prev);
  };

  return (
    <TextInput
      type={showPassword ? 'text' : 'password'}
      buttonAction={toggleShowPassword}
      button={
        <IconButton
          svg={showPassword ? EyeVisibleSVG : EyeHiddenSVG}
          aria-label={showPassword ? 'Hide password' : 'Show password'}
        />
      }
      {...inputProps}
    ></TextInput>
  );
};

export default PasswordInput;
